.user-dashboard {
    .stats {
        .stat-container {
            border: 1px solid #f5f5f5;
            padding: 10px 0;

            .large {
                color: #1ab394;
                text-align: center;
                font-size: 30px;
            }

            .small {
                color: #555;
                text-align: center;
                font-size: 14px;
            }
        }
    }

    .collapse {
        .collapse-content {
            color: #d62c1a;
        }
    }
    
    .task-title {
        font-weight: initial;
        font-size: 16px;
        margin: 5px 0;
    }

    .page-header {
        margin-bottom: 8px;
        padding-bottom: 0;
    }

    .progress {
        margin-bottom: 0;
    }
}

.chart-panel {
    .panel-body {
        padding: 15px 0;
        border-top: 1px solid #f5f5f5;
    }
}

.user-avatar {
    display: block;
    text-align: center;
    .user-avatar-picture {
        border-radius: 4px;
    }
}

.user-profile-wrapper {
    .panel {
        border-top: 1px solid #f5f5f5;
    }
}

.task-dashboard-control {
    display: inline-block;
    margin-right: 10px;
    cursor: pointer;
    color: #111;
}

.failed-tasks-panel {
    margin-top: 36px;
}
